<template>
    <div class="publishArticle">
        <el-row class="tabel_warp">
            <el-col :span="24">
                <search v-on:search="search_btn" ref="searchComponent" :searchData="searchData"></search>
                <div class="grid-content">
                    <el-table :data="tableData" border style="width: 100%">
                        <el-table-column prop="id" label="序号" width="80" align="center">
                        </el-table-column>
                        <el-table-column prop="title" label="标题" width="160" align="center" :show-overflow-tooltip=true>
                        </el-table-column>
                        <el-table-column prop="sort" label="分类" width="120" align="center">
                        </el-table-column>
                        <el-table-column prop="" label="发布日期" width="190" align="center">
                            <template slot-scope="tableData">
                                {{tableData.row.releasetime | moment}}
                            </template>
                        </el-table-column>
                        <!-- <el-table-column prop="releasename" label="编辑人" width="95" align="center">
                        </el-table-column> -->
                        <el-table-column prop="staus" label="状态" width="80" align="center">
                            <template slot-scope="tableData">
                                <font v-if="tableData.row.staus == 1" color='#547BE8'>待审核</font>
                                <font v-else-if="tableData.row.staus == 2" color='#15BE9D'>已发布</font>
                                <!-- <font v-else-if="tableData.row.staus == 2" color='#F52653'>已驳回</font>
                                <font v-else color='#414142'>草稿箱</font> -->
                            </template>
                        </el-table-column>
                        <el-table-column prop="reviewname" label="审核人" width="90" align="center">
                        </el-table-column>
                        <el-table-column prop="top" label="是否置顶" width="150" align="center">
                            <template slot-scope="tableData">
                                <el-radio-group v-show="tableData.row.staus == 2" v-model="tableData.row.top" @change="radioChange(tableData.row.id,tableData.row.top)">
                                    <el-radio :label="2">是</el-radio>
                                    <el-radio :label="1">否</el-radio>
                                </el-radio-group>
                            </template>
                        </el-table-column>
                        <el-table-column prop="address" label="操作" width="170" align="center">
                            <template slot-scope="tableData">
                                <el-button v-show="tableData.row.staus == 2" size="mini" @click="handleSee(tableData.$index, tableData.row)" class="seeArticle">查看</el-button>
                                <el-button v-show="tableData.row.staus == 1" size="mini" type="danger" @click="handleArticle(tableData.$index, tableData.row)" class="examineArticle">审核</el-button>
                                <el-button v-show="tableData.row.staus == 2" size="mini" type="danger" @click="handleDelete(tableData.$index, tableData.row)" class="deleteArticle">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <el-pagination
                    :current-page.sync="searchContent.page"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size.sync="searchContent.limit"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    @current-change="FindAll"
                    @size-change="FindAll">
                </el-pagination>
            </el-col>
        </el-row>
        <examineMessage ref="examineMessage" :parameter="parameter"></examineMessage> <!--编辑弹出框 -->
        <seeMessage ref="seeMessage" :parameter="parameter"></seeMessage> <!--查看弹出框 -->
    </div>
</template>

<script>
import { findAllArticle,reviewArticle,selectAllSort,allArticleSort,findStausArticle,toTop,unTop } from '../../service/index'
import seeMessage from '../MessageBox/seeMessage'
import examineMessage from '../MessageBox/examineMessage'
export default {
    name:'publishArticle',
    data() {
        return {
            publishDialogVisible:false,  //编辑对话框
            seePublishDialogVisible:false,  //查看对话框
            parameter:{},
            total:0,
            searchData:{
                publisher: true,
                status: true,
                releasetime: true,
                sort: {
                    countName: allArticleSort  // 分类接口
                }
            },
            searchContent:{
                page: 1,
                limit: 10,
            },
            tableData: [],
            index:'1'
        }
    },
    components:{
        examineMessage,
        seeMessage
    },
    methods: {
        search_btn: function (somedata){   //点击搜索按钮传输搜索条件
            for(var i in somedata[0]){
                this.searchContent[i] = somedata[0][i]
            }
            this.searchContent.staus = this.searchContent.status
            delete this.searchContent.status
            this.FindAll();
        },
        FindAll(){        //查询表格所有数据
            findStausArticle(this.searchContent).then(res => {
                // 后台是传过来的自增长的top来确定那个是置顶的内容,取消置顶为1，置顶会自增长,为了选中单选框，将所有置顶的都赋值为2
                for (var i in res.data.data){
                    if(res.data.data[i].top > 1){
                        res.data.data[i].top = 2
                    }
                }
                this.tableData = res.data.data
                this.total = res.data.count
            })
        },
        handleArticle(index, row) {   //审核文章
            this.$refs.examineMessage.examineMessageDialogVisible = true;
            this.parameter = {
                title:'审核文章',
                data: row,
                fun:reviewArticle
            }
        },
        handleDelete(index, row) {
            this.$confirm('删除后将不能恢复，确定要删除系统中的这条记录吗?', '删除', {
            dangerouslyUseHTMLString: true,
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            center: true,
            customClass: 'deleteDlog',
            cancelButtonClass: 'closeMessage',
            confirmButtonClass: 'DetermineMessage',
            }).then(() => {
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
            this.tableData.splice(index,1);
            console.log(index, row);
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
            });
        },
        handleSee(index, row){
            console.log(index,row);
            this.parameter = {
                title:'查看',
                data: row
            }
            this.$refs.seeMessage.seePublishDialogVisible = true;
        },
        DetermineBtn(){
            
        },
        radioChange(id,val){
            if(val > 1){
                toTop({id:id}).then(res => {
                    if(res.status == 200){
                        this.$message.success('置顶成功')
                    }else{
                        this.$message.error('置顶失败')
                    }
                })
            }else{
                unTop({id:id}).then(res => {
                    if(res.status == 200){
                        this.$message.success('取消置顶')
                    }else{
                        this.$message.error('取消失败')
                    }
                })
            }
        }
    },
    beforeMount(){
        this.FindAll()
    }
}
</script>

<style lang='scss' scoped>
    .publishArticle{
        width: px2rem(1620);
        height: px2rem(900);
        padding: px2rem(22) px2rem(22) 0 px2rem(22);
        float: left;
        .tabel_warp{
            padding: px2rem(22) 0 0 px2rem(30);
            height: 100%;
            background: #ffffff;
            margin-top: px2rem(22);
            .search_warp{
                margin-bottom: px2rem(22);
            }
            .grid-content{
                display: inline-block;
                .el-table__header{
                    padding-left: px2rem(31);
                    .cell{
                        text-align: center !important;
                    }
                }
                @mixin articleBtn(){
                    color: #ffffff;
                    border: none;
                    padding: 7px 17px;
                }
                .examineArticle{
                    background: #547BE8;
                    float: left;
                    @include articleBtn();
                }
                .seeArticle{
                    background: #15BE9D;
                    @include articleBtn();
                }
                .deleteArticle{
                    background: #F52653;
                    @include articleBtn();
                }
            }
            .editor{
                margin-left: px2rem(10);
            }
            .search_btn{
                width: px2rem(80);
                background: #547BE8;
                color: #ffffff;
            }
        }
        
    }
</style>